html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: rgb(218, 213, 213);
  justify-content: center;
  word-break: break-word;
}
.form {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.form__text {
    width: 80%;
    height: 40px;
    margin-right: 20px;
    border: 2px solid #dad5d5;
    padding-left: 10px;
}

.form__button {
    width: 20%;
    height: 40px;
    background-color: teal;
    color: white;
    border: 2px solid teal;
    cursor: pointer;
}

.form__button:hover {
    transition: 1s;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    background-color: rgb(41, 141, 141);
}

@media (max-width: 767px) {
    .form {
        display: flex;
        flex-direction: column;
    }

    .form__text {
        width: 100%;
        margin-bottom: 10px;
    }

    .form__button {
        width: 100%;
    }
}
.list {
    width: 100%;
    list-style: none;
    padding: 20px;
}

.list__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-auto-rows: minmax(40px, auto);
    align-items: center;
    border-bottom: 1px solid #dad5d5;
    padding: 10px 0px;
    visibility: visible;
}

.list__item--hidden {
    display: none;
}

.list__taskText {
    margin: 0px 10px;
}

.list__taskText--done {
    text-decoration: line-through;
}

.list__icon {
    width: 30px;
    height: 30px;
    color: white;
    cursor: pointer;
    border: none;
}

.list__icon--green {
    background-color: hsl(120, 50%, 41%);
}

.list__icon--green:hover {
    background-color: hsl(120, 36%, 47%);
}

.list__icon--red {
    background-color: rgb(199, 26, 26)
}

.list__icon--red:hover {
    background-color: hsl(0, 49%, 50%)
}
.buttons__container {
    display: flex;
    flex-wrap: wrap;
}

.buttons__button {
    border: none;
    background-color: transparent;
    color: gray;
    font-size: 17px;
    margin-left: 20px;
    cursor: pointer;
}

@media (max-width: 767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }
}

.buttons__button:hover {
    transition: color 1.5s;
    color: #cecccc;
}

.buttons__button:disabled {
    color: #cecccc;
}
.section {
    background-color: white;
}

.section__header {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    font-size: 20px;
    font-weight: 700;
    border-bottom: 1px solid rgb(218, 213, 213);
}

@media (max-width: 767px) {
    .section__header {
        display: grid;
        grid-template-columns: 1fr;           
    }
}
.main {    
    max-width: 900px;
    padding: 10px;
    margin: 10px auto;
}
